/**
 * Generates financial scenarios for simulation
 * @param {object} currentSituation - User's current financial situation
 * @param {string} scenarioType - Type of scenario to generate
 * @returns {Promise<object>} Scenario data
 */
export async function generateScenario(currentSituation, scenarioType) {
  try {
    const prompt = `Gnrez un scnario financier "${scenarioType}" bas sur cette situation:

Situation actuelle:
${JSON.stringify(currentSituation, null, 2)}

Crez un scnario raliste qui montre:
- Les changements proposs
- L'impact financier estim
- Les tapes de mise en uvre
- Les risques et opportunits

Rpondez en franais avec des donnes chiffres prcises.`;

    const response = await groq.chat.completions.create({
      model: 'llama-3.1-70b-versatile',
      messages: [
        { 
          role: 'system', 
          content: 'Vous tes un planificateur financier expert. Vous crez des scnarios ralistes et actionnables.' 
        },
        { role: 'user', content: prompt },
      ],
      temperature: 0.4,
      max_tokens: 600,
    });

    return parseScenarioFromResponse(response.choices[0].message.content, scenarioType);
  } catch (error) {
    logger.error('Error generating scenario:', error);
    throw new Error('Erreur lors de la gnration du scnario.');
  }
}

/**
 * Streaming chat completion for real-time financial advice
 * @param {string} message - User's message
 * @param {Function} onChunk - Callback for each streamed chunk
 * @param {Array} conversationHistory - Previous conversation context
 */
export async function streamFinancialAdvice(message, onChunk, conversationHistory = []) {
  try {
    const messages = [
      { 
        role: 'system', 
        content: 'Vous tes un conseiller financier expert en neurosciences comportementales. Fournissez des conseils personnaliss, bienveillants et bass sur la science.' 
      },
      ...conversationHistory,
      { role: 'user', content: message }
    ];

    const stream = await groq.chat.completions.create({
      model: 'llama-3.1-70b-versatile',
      messages,
      stream: true,
      temperature: 0.7,
      max_tokens: 800,
    });

    for await (const chunk of stream) {
      const content = chunk.choices[0]?.delta?.content || '';
      if (content) {
        onChunk(content);
      }
    }
  } catch (error) {
    logger.error('Error in streaming financial advice:', error);
    throw new Error('Erreur lors de la gnration des conseils en temps rel.');
  }
}

// Helper functions
function extractCategory(analysisText) {
  // Simple category extraction based on keywords
  const categories = {
    'motionnel': ['motion', 'stress', 'anxit', 'humeur'],
    'habitudes': ['habitude', 'routine', 'comportement', 'pattern'],
    'budget': ['budget', 'dpense', 'pargne', 'argent'],
    'investissement': ['investir', 'placement', 'rendement', 'risque']
  };

  for (const [category, keywords] of Object.entries(categories)) {
    if (keywords.some(keyword => analysisText.toLowerCase().includes(keyword))) {
      return category;
    }
  }
  return 'Gnral';
}

function extractInsights(analysisText) {
  // Extract key insights from the analysis
  return [
    {
      type: 'behavioral',
      message: 'Pattern comportemental identifi dans votre question',
      confidence: 0.8
    }
  ];
}

function parseInsightsFromResponse(responseText) {
  // Parse structured insights from AI response
  try {
    // Try to extract structured content, fallback to parsed text
    const insights = [];
    const lines = responseText.split('\n').filter(line => line.trim());
    
    let currentInsight = null;
    for (const line of lines) {
      if (line.includes('**') || line.includes('#')) {
        if (currentInsight) {
          insights.push(currentInsight);
        }
        currentInsight = {
          title: line.replace(/[*#]/g, '').trim(),
          description: '',
          actions: []
        };
      } else if (line.trim() && currentInsight) {
        if (line.includes('-') || line.includes('')) {
          currentInsight.actions.push(line.replace(/[-]/g, '').trim());
        } else {
          currentInsight.description += line.trim() + ' ';
        }
      }
    }
    
    if (currentInsight) {
      insights.push(currentInsight);
    }
    
    return insights.length > 0 ? insights : [
      {
        title: 'Analyse personnalise gnre',
        description: responseText.substring(0, 200) + '...',
        actions: ['Consultez l\'analyse complte pour plus de dtails']
      }
    ];
  } catch (error) {
    return [
      {
        title: 'Insights gnrs',
        description: responseText,
        actions: []
      }
    ];
  }
}

function parseEquationFromResponse(responseText) {
  // Parse equation data from AI response
  try {
    // Try to parse JSON if present, otherwise create structured equation
    if (responseText.includes('{') && responseText.includes('}')) {
      const jsonMatch = responseText.match(/{[\s\S]*}/);
      if (jsonMatch) {
        return JSON.parse(jsonMatch[0]);
      }
    }
    
    // Fallback: create equation from text analysis
    return {
      formula: "Habitudes  tat motionnel = Impact Financier",
      variables: [
        {
          id: 'habits',
          name: 'Habitudes de Dpense',
          value: 7.5,
          unit: '/10',
          impact: 0.8,
          color: '#EF4444',
          description: 'Score bas sur vos patterns de dpense'
        },
        {
          id: 'emotional_state',
          name: 'tat motionnel',
          value: 6.0,
          unit: '/10',
          impact: 0.7,
          color: '#F59E0B',
          description: 'Influence motionnelle sur les dcisions'
        },
        {
          id: 'financial_impact',
          name: 'Impact Financier',
          value: 450,
          unit: '',
          impact: 1.0,
          color: '#DC2626',
          description: 'Impact mensuel estim'
        }
      ],
      insights: [
        {
          id: 1,
          type: 'correlation',
          strength: 0.85,
          message: responseText.substring(0, 100) + '...'
        }
      ]
    };
  } catch (error) {
    logger.error('Error parsing equation:', error);
    return null;
  }
}

function parseScenarioFromResponse(responseText, scenarioType) {
  // Parse scenario data from AI response
  return {
    name: `Scnario ${scenarioType}`,
    description: responseText.substring(0, 200) + '...',
    impact: {
      balanceChange: Math.floor(Math.random() * 400) + 100,
      timeframe: '3 mois',
      probability: 0.75
    },
    variables: [], // Would be populated based on actual parsing
    steps: responseText.split('\n').filter(line => 
      line.includes('-') || line.includes('')
    ).map(step => step.replace(/[-]/g, '').trim()).slice(0, 5)
  };
}